<h1 id="配置" tabindex="-1"><a class="header-anchor" href="#配置" aria-hidden="true">#</a> 配置</h1>
<p>下面是一个完整的配置样例：</p>
<blockquote>
<p>不建议你在配置的时候弄这么多，用到啥就配置啥才是最好的，因为大部分用默认值即可。</p>
</blockquote>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token punctuation">[</span>
    <span class="token doc-comment comment">/**
     * 账号基本信息，请从微信公众平台/开放平台获取
     */</span>
    <span class="token string single-quoted-string">'app_id'</span>  <span class="token operator">=></span> <span class="token string single-quoted-string">'your-app-id'</span><span class="token punctuation">,</span>         <span class="token comment">// AppID</span>
    <span class="token string single-quoted-string">'secret'</span>  <span class="token operator">=></span> <span class="token string single-quoted-string">'your-app-secret'</span><span class="token punctuation">,</span>     <span class="token comment">// AppSecret</span>
    <span class="token string single-quoted-string">'token'</span>   <span class="token operator">=></span> <span class="token string single-quoted-string">'your-token'</span><span class="token punctuation">,</span>          <span class="token comment">// Token</span>
    <span class="token string single-quoted-string">'aes_key'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">''</span><span class="token punctuation">,</span>                    <span class="token comment">// EncodingAESKey，兼容与安全模式下请一定要填写！！！</span>

    <span class="token doc-comment comment">/**
     * 日志配置 (默认不启用日志)
     *
     * level: 日志级别, 可选为：
     *         debug/info/notice/warning/error/critical/alert/emergency
     * path：日志文件位置(绝对路径!!!)，要求可写权限
     */</span>
    <span class="token comment">// 'logging' => [</span>
    <span class="token comment">//     'default' => 'dev', // 默认使用的 channel，生产环境可以改为下面的 prod</span>
    <span class="token comment">//     'channels' => [</span>
    <span class="token comment">//         // 测试环境</span>
    <span class="token comment">//         'dev' => [</span>
    <span class="token comment">//             'driver' => 'single',</span>
    <span class="token comment">//             'path' => '/tmp/easywechat.log',</span>
    <span class="token comment">//             'level' => 'debug',</span>
    <span class="token comment">//         ],</span>
    <span class="token comment">//         // 生产环境</span>
    <span class="token comment">//         'prod' => [</span>
    <span class="token comment">//             'driver' => 'daily',</span>
    <span class="token comment">//             'path' => '/tmp/easywechat.log',</span>
    <span class="token comment">//             'level' => 'info',</span>
    <span class="token comment">//         ],</span>
    <span class="token comment">//     ],</span>
    <span class="token comment">// ],</span>

    <span class="token doc-comment comment">/**
     * 接口请求相关配置，超时时间等，具体可用参数请参考：
     * https://github.com/symfony/symfony/blob/5.3/src/Symfony/Contracts/HttpClient/HttpClientInterface.php
     */</span>
    <span class="token string single-quoted-string">'http'</span> <span class="token operator">=></span> <span class="token punctuation">[</span>
        <span class="token string single-quoted-string">'timeout'</span> <span class="token operator">=></span> <span class="token number">5.0</span><span class="token punctuation">,</span>
        <span class="token comment">// 'base_uri' => 'https://api.weixin.qq.com/', // 如果你在国外想要覆盖默认的 url 的时候才使用，根据不同的模块配置不同的 uri</span>
    <span class="token punctuation">]</span><span class="token punctuation">,</span>
<span class="token punctuation">]</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br><span class="line-number">21</span><br><span class="line-number">22</span><br><span class="line-number">23</span><br><span class="line-number">24</span><br><span class="line-number">25</span><br><span class="line-number">26</span><br><span class="line-number">27</span><br><span class="line-number">28</span><br><span class="line-number">29</span><br><span class="line-number">30</span><br><span class="line-number">31</span><br><span class="line-number">32</span><br><span class="line-number">33</span><br><span class="line-number">34</span><br><span class="line-number">35</span><br><span class="line-number">36</span><br><span class="line-number">37</span><br><span class="line-number">38</span><br><span class="line-number">39</span><br><span class="line-number">40</span><br><span class="line-number">41</span><br><span class="line-number">42</span><br><span class="line-number">43</span><br></div></div><blockquote>
<p>❤️ 安全模式下请一定要填写 <code>aes_key</code></p>
</blockquote>
